import { Component, OnInit } from '@angular/core';
import { English } from "../../i18n/english";
//import { English2 } from '../../i18n/english2';
import { FrameGroup, FrameTemplate } from './framegroup';

@Component({
  selector: 'app-game',
  templateUrl: './game.component.html',
  styleUrls: ['./game.component.css']
})
export class GameComponent implements OnInit {
  username = localStorage.getItem('username');
  lang = new English();
  lang2;
  textContent = '';
  speaker = '';
  txtbox = {
    visible: true
  };
  chapter: FrameGroup[];
  current: FrameGroup;
  ended = false;

  charIds = {
    kitsu_chiri: {
        displayName: 'Kitsu-san',
    },
    fuura_kafuka: {
        displayName: 'Fuura-san'
    }
  };
  players = {
    p1: {
      getName: () => localStorage.getItem('username')
    }
  };

  constructor() {
    // this.lang2 = new English2(this.charIds, this.players);
    // this.chapter = FrameGroup.mkframes(this.lang.ch1);
    // this.current = this.chapter[0];
  }

  ngOnInit() {
    localStorage.setItem('global.navbar', 'false');
    const newGame = JSON.parse(localStorage.getItem('global.newgame')) || this.lang.ch2;
    this.chapter = FrameGroup.mkframes(newGame);
    this.current = this.chapter[0];
    this.setText();
  }

  noNext(): boolean {
    return this.current.options !== undefined || !this.current.hasNext();
  }

  next(option?: any) {
    if (this.current.hasNext()) this.current = this.current.next(option);
    this.setText();
  }

  prev() {
    this.current = this.current.prev();
    this.setText();
    document.getElementById('img0_0').style.display = 'none';
    //document.getElementById('img1_0').style.display = 'initial';
  }

  end() {
    if (!this.ended) {
      document.getElementById('img0_0').style.display = 'grid';
      //document.getElementById('img1_0').style.display = 'none';
      const hackFrame: FrameGroup = new FrameGroup();
      hackFrame.displayName = 'rmbdev';
      hackFrame.prevFrame = this.current;
      hackFrame.text = `Thanks for your time!!!
        Credits: programming, character art, background art, writing: rmbdev
        Special credits: Sayonara Zetsubou Sensei (character design)`
      this.current = hackFrame;
      this.setText();
      this.ended = true;
    } else {
      document.getElementById('img0_0').style.display = 'none';
      //document.getElementById('img1_0').style.display = 'initial';
      this.current = this.chapter[0];
      this.ended = false;
      this.setText();
    }
  }

  setText() {
    this.speaker = this.current.displayName ? this.current.displayName.replace(/%s/, this.username) : '';
    this.textContent = this.current.text.replace(/%s/, this.username);
  }
}



