class FrameTemplate {
  parent?: FrameTemplate;
  children?: FrameTemplate[];
  charId?: string;
  displayName?: string;
  text?: string;
  options?: OptionTemplate[];
}

class OptionTemplate {
  label: string;
  children: FrameTemplate[];
}

class FrameGroup extends FrameTemplate {
  prevFrame?: FrameGroup;
  nextFrame?: FrameGroup;

  constructor(template?: FrameTemplate, parent?: FrameGroup) {
    super();
    if (parent) {
      this.charId = parent.charId;
      this.displayName = parent.displayName;
    }
    if (template) {
      Object.assign(this, template);
    }
  }

  static mkframes(data: FrameTemplate[]): FrameGroup[] {
    const result = this.buildFrames(data);
    return result;
  }

  static buildFrames(data: FrameTemplate[], parent?: FrameGroup, aResult?: FrameGroup[]): FrameGroup[] {
    let current: FrameGroup;
    const result: FrameGroup[] = aResult ? aResult : [];

    data.forEach(frame => {
      let currentFrame = new FrameGroup(frame, parent);
      if (frame.text) {
        current = currentFrame;
        if (result.length > 0) {
          const latest = result[result.length - 1];
          latest.nextFrame = current;
          current.prevFrame = latest;
        }
        if (frame.options) {
          currentFrame.next = (option) => {
            currentFrame.children = currentFrame.options.filter(o => o === option)[0].children;
            const tree = this.buildFrames(currentFrame.children, currentFrame);
            tree[0].prevFrame = currentFrame;
            tree[tree.length - 1].nextFrame = currentFrame.nextFrame;
            return tree[0];
          }
        }
        result.push(current);
      } else {
        current = undefined;
      }
      if (frame.children) {
        this.buildFrames(frame.children, currentFrame, result);
      }
    });
    return result;
  }

  static mkchars(data: FrameGroup[]) {
    const result: CharGroup[] = [];
    data.forEach(frame => {
      if (frame.text) {
        const sthg = frame.text.replace(/a/, '');
        const chg = new CharGroup();
        chg.text = sthg;
        result.push(chg);
      }
    });
    return result;
  }

  public hasNext() {
    return !!this.options || !!this.nextFrame;
  }

  next(option?: any) {
    return this.nextFrame;
  }

  prev() {
    return this.prevFrame;
  }
}

class CharGroup {
  text: string = '';
}

export { FrameGroup, FrameTemplate };



